export interface StudentApiItem {
  id: number
  AdmissionNo: string
  student_name: string
  ParentNameFather?: string
  Mother?: string
  address?: string
  OccupationFather?: string
  SchoolClass?: string
  TCNo?: string
  AdmissionDate?: string
  DateOfBirth?: string
  Nationlty?: string
  Relegion?: string
  Caste?: string
  MotherToung?: string
  ClassStudentAdmitted?: string
  LeavingClass?: string
  DateOfLeaving?: string
  ReasonOfLeaving?: string
  NoAndDateTransferCertificate?: string
  AdharNo?: string
  mobile?: string
  email?: string
  status?: string
  EntryDate?: string
}

export interface StudentListApiResponse {
  status: boolean
  message: string
  data: StudentApiItem[]
  filters: {
    status: string
    search: string
    from_date: string
    to_date: string
  }
  pagination: {
    total: number
    page: number
    limit: number
  }
}

const BASE_URL = process.env.NEXT_PUBLIC_API_BASE_URL!
const API_KEY = process.env.NEXT_PUBLIC_API_KEY!

export async function getStudentListApi(
  page: number,
  limit: number,
  search = ""
): Promise<StudentListApiResponse> {
  const token = localStorage.getItem("auth_token")
  if (!token) throw new Error("Unauthorized")

  const res = await fetch(
    `${BASE_URL}/student/api/student_list?page=${page}&limit=${limit}&search=${search}`,
    {
      headers: {
        Accept: "application/json",
        Authorization: `Bearer ${token}`,
        "X-API-Key": API_KEY,
      },
    }
  )

  const data = await res.json()

  if (!data.status) {
    throw new Error(data.message || "Failed to fetch students")
  }

  return data
}
